/* Tailwind-like utility classes */

/* ReactFlow CSS - Minimal required styles */
.react-flow {
  width: 100%;
  height: 100%;
  position: relative;
}

.react-flow__renderer {
  width: 100%;
  height: 100%;
}

.react-flow__panel {
  position: absolute;
  z-index: 5;
  margin: 15px;
}

.react-flow__panel.bottom {
  bottom: 0;
}

.react-flow__panel.left {
  left: 0;
}

.react-flow__panel.right {
  right: 0;
}

.react-flow__panel.top {
  top: 0;
}

.react-flow__nodes {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

/* Display */
.flex { display: flex; }
.hidden { display: none; }
.block { display: block; }
.inline-block { display: inline-block; }

/* Flex Direction */
.flex-row { flex-direction: row; }
.flex-col { flex-direction: column; }

/* Flex Wrap */
.flex-wrap { flex-wrap: wrap; }

/* Flex */
.flex-1 { flex: 1 1 0%; }
.grow { flex-grow: 1; }

/* Justify Content */
.justify-start { justify-content: flex-start; }
.justify-end { justify-content: flex-end; }
.justify-center { justify-content: center; }
.justify-between { justify-content: space-between; }

/* Align Items */
.items-start { align-items: flex-start; }
.items-end { align-items: flex-end; }
.items-center { align-items: center; }
.items-baseline { align-items: baseline; }

/* Width */
.w-full { width: 100%; }
.w-auto { width: auto; }
.w-4 { width: 1rem; }
.w-6 { width: 1.5rem; }
.w-8 { width: 2rem; }
.w-120 { width: 120px; }
.w-200 { width: 200px; }
.w-250 { width: 250px; }
.w-500 { width: 500px; }

/* Height */
.h-full { height: 100%; }
.h-auto { height: auto; }
.h-4 { height: 1rem; }
.h-6 { height: 1.5rem; }
.h-8 { height: 2rem; }
.h-30 { height: 30px; }
.h-120 { height: 120px; }
.h-24 { height: 6rem; }

/* Max Height */
.max-h-\\[calc\\(100\\%-10px\\)\\] { max-height: calc(100% - 10px); }
.max-h-\\[50vh\\] { max-height: 50vh; }

/* Min Width */
.min-w-\\[250px\\] { min-width: 250px; }

/* Margin */
.m-0 { margin: 0; }
.mt-1 { margin-top: 0.25rem; }
.mt-2 { margin-top: 0.5rem; }
.mt-4 { margin-top: 1rem; }
.mb-2 { margin-bottom: 0.5rem; }
.mb-3 { margin-bottom: 0.75rem; }
.mb-4 { margin-bottom: 1rem; }
.ml-1 { margin-left: 0.25rem; }
.ml-4 { margin-left: 1rem; }
.mr-1 { margin-right: 0.25rem; }
.mr-2 { margin-right: 0.5rem; }
.-my-2 { margin-top: -0.5rem; margin-bottom: -0.5rem; }

/* Padding */
.p-0 { padding: 0; }
.p-0\\.5 { padding: 0.125rem; }
.p-2 { padding: 0.5rem; }
.p-3 { padding: 0.75rem; }
.p-\\[10px\\] { padding: 10px; }
.px-1 { padding-left: 0.25rem; padding-right: 0.25rem; }
.py-1 { padding-top: 0.25rem; padding-bottom: 0.25rem; }
.pb-2 { padding-bottom: 0.5rem; }

/* Spacing */
.space-x-1 > * + * { margin-left: 0.25rem; }
.space-x-3 > * + * { margin-left: 0.75rem; }
.space-x-4 > * + * { margin-left: 1rem; }
.space-x-6 > * + * { margin-left: 1.5rem; }
.space-y-1 > * + * { margin-top: 0.25rem; }
.space-y-1\\.5 > * + * { margin-top: 0.375rem; }

/* Gap */
.gap-16 { gap: 4rem; }

/* Position */
.relative { position: relative; }
.absolute { position: absolute; }
.fixed { position: fixed; }

/* Inset */
.inset-0 { top: 0; right: 0; bottom: 0; left: 0; }
.top-0 { top: 0; }
.bottom-2 { bottom: 0.5rem; }
.right-2 { right: 0.5rem; }

/* Z-Index */
.z-50 { z-index: 50; }
.z-\\[1000\\] { z-index: 1000; }
.z-\\[9999\\] { z-index: 9999; }

/* Overflow */
.overflow-hidden { overflow: hidden; }
.overflow-auto { overflow: auto; }
.overflow-y-auto { overflow-y: auto; }

/* Text Alignment */
.text-left { text-align: left; }
.text-center { text-align: center; }
.text-right { text-align: right; }

/* Font Size */
.text-xs { font-size: 0.75rem; line-height: 1rem; }
.text-sm { font-size: 0.875rem; line-height: 1.25rem; }
.text-base { font-size: 1rem; line-height: 1.5rem; }
.text-lg { font-size: 1.125rem; line-height: 1.75rem; }

/* Font Weight */
.font-medium { font-weight: 500; }
.font-semibold { font-weight: 600; }
.font-bold { font-weight: 700; }

/* Line Height */
.leading-tight { line-height: 1.25; }

/* Text Color */
.text-white { color: #ffffff; }
.text-da-primary-500 { color: #2563eb; }
.text-da-gray-dark { color: #374151; }
.text-da-gray-medium { color: #6b7280; }
.text-blue-500 { color: #3b82f6; }
.text-blue-600 { color: #2563eb; }
.text-red-500 { color: #ef4444; }

/* Background Color */
.bg-white { background-color: #ffffff; }
.bg-white\\/95 { background-color: rgba(255, 255, 255, 0.95); }
.bg-gray-100 { background-color: #f3f4f6; }
.bg-gray-200 { background-color: #e5e7eb; }
.bg-red-100 { background-color: #fee2e2; }
.bg-da-primary-100 { background-color: #dbeafe; }

/* Border */
.border { border-width: 1px; }
.border-b { border-bottom-width: 1px; }
.border-gray-200 { border-color: #e5e7eb; }

/* Border Radius */
.rounded { border-radius: 0.25rem; }
.rounded-md { border-radius: 0.375rem; }
.rounded-lg { border-radius: 0.5rem; }
.rounded-xl { border-radius: 0.75rem; }
.rounded-2xl { border-radius: 1rem; }
.rounded-full { border-radius: 9999px; }

/* Shadow */
.shadow-sm { box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05); }
.shadow-md { box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06); }
.shadow-lg { box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05); }

/* Cursor */
.cursor-pointer { cursor: pointer; }

/* Transition */
.transition-all { transition-property: all; transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1); transition-duration: 150ms; }
.duration-300 { transition-duration: 300ms; }
.ease-in-out { transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1); }

/* Transform */
.scale-105 { transform: scale(1.05); }

/* Hover States */
.hover\\:bg-gray-100:hover { background-color: #f3f4f6; }
.hover\\:bg-red-100:hover { background-color: #fee2e2; }
.hover\\:bg-da-primary-100:hover { background-color: #dbeafe; }
.hover\\:text-red-500:hover { color: #ef4444; }
.hover\\:text-da-primary-500:hover { color: #2563eb; }
.hover\\:scale-105:hover { transform: scale(1.05); }
.hover\\:shadow-lg:hover { box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05); }

/* Text Decoration */
.underline { text-decoration: underline; }
.capitalize { text-transform: capitalize; }

/* Text Overflow */
.text-ellipsis { text-overflow: ellipsis; }
.whitespace-nowrap { white-space: nowrap; }

/* Object Fit */
.object-contain { object-fit: contain; }

/* Pointer Events */
.pointer-events-none { pointer-events: none; }

/* Animation */
.animate-pulse { animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite; }
.animate-node-appear { animation: nodeAppear 0.5s ease-out; }

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

@keyframes nodeAppear {
  0% { opacity: 0; transform: scale(0); }
  100% { opacity: 1; transform: scale(1); }
}

/* Scroll */
.scroll-gray::-webkit-scrollbar {
  width: 8px;
}
.scroll-gray::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.scroll-gray::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 4px;
}
.scroll-gray::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* Size utilities */
.size-3\\.5 { width: 0.875rem; height: 0.875rem; }
.size-4 { width: 1rem; height: 1rem; }

/* Utility helper */
.\\!justify-start { justify-content: flex-start !important; }
.\\!h-6 { height: 1.5rem !important; }
.\\!p-2 { padding: 0.5rem !important; }
.\\!text-xs { font-size: 0.75rem !important; line-height: 1rem !important; }

